/* width */
::-webkit-scrollbar {
    width: 9px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #a6a6a6;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #636363; 
    border-radius: 10px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #545454; 
  }